.card {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  align-content: center;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  display: flex;
  flex-direction: row;
  width: 300px;
  border-radius: 6px;
  background-color: white;
}
  
  .card:hover {
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.685);
  }
  
  img {
    max-height: 170px;
  }

  .container {
    padding: 2px 16px;
  }

  .card-info{
    margin: 3px;
    display: flex;
    flex-direction: column;
  }

  .label-gray{
    font-size: 14px;
    color: rgb(161, 161, 161);
    font-weight: bold;
  }
  .label-black{
    font-size: 12px;
    color: rgb(43, 43, 43);
    font-weight: bold;
  }

  button {
    font-weight: bold;
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: #109cbe;
    border: 0;
    padding: 5px;
    color: #FFFFFF;
    font-size: 14px;
    transition: all 0.3 ease;
    cursor: pointer;
}
.card-grid {
    margin-top: 10px;
    margin-left: 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.header {
    padding: 0.8rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: rgb(182, 182, 182);
}

.search{
    padding: 0.2rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}


.search input {
    background: #f2f2f2;
    border: 0;
    padding: 0.2rem;
    box-sizing: border-box;
    font-size: 14px;
}


.video {
    position: relative;
    z-index: 1;
    text-align: center;
}
